@import 'colors';
@import 'fonts';

$left-margin: 5%;
$top-margin: 2%;

.main-wrapper {
  // background: $main-white cover no-repeat;
  // height: 100%;
  // width: 100%;
}

//-------TOP

.h-top {
  background: url('../images/home/home top image.jpg') no-repeat;
  background-size: cover;
  height: 135.5vh;
  width: 100%;
  position: relative;
}

.logo {
  background: url('../images/home/logo-1.svg') no-repeat;
  height: 9.5%;
  // width: 16%;
  width: 19%;
  position: absolute;
  margin-left: $left-margin;
  margin-top: $top-margin;
}

.explore {
  display: block;
  position: absolute;
  right: 0;
  margin-top: $top-margin;
  margin-right: 120px; //change when i get the 3 lines
  display: flex;
  h1 {
    font-size: 270%;
    font-weight: bolder;
    font-family: $oswald;
    color: $tradition-white;
    margin-right: 6%;
  }
  i {
    font-size: 270%;
    color: $tradition-white;
  }
}

.aggie-tradition {
  margin-left: $left-margin;
  margin-top: 18%;
  position: absolute;
}

.aggie {
  display: flex;
  justify-content: space-between;
  width: 25vw;
  h4 {
    color: $aggie-tan;
    font-weight: bold;
    font-size: 114%;
    font-family: $bevan;
    // margin-left: 1%;
    margin-right: 49%;
    text-shadow: 1px 1px $shadow;
  }
}

.small-box {
  background: $aggie-tan;
  height: 0.80vh;
  width: 5vw;
  margin-top: 1.5%;
  text-shadow: 1px 1px $shadow;
}

// .aggie {
//   width: 25vw;
// }

.tradition {
  margin-top: 3.85%;
  font-family: $oswald;
  h1 {
    font-size: 773%;
    color: $tradition-white;
    font-weight: bolder;
    text-shadow: 2px 2px $shadow;
  }
}

.tradition > h1 {
  margin-bottom: 2.45%;
}

.college-station-div {
  height: 2.5vh;
  width: 100%;
  background: $college-station-brown;
  position: absolute;
  margin-top: 43.12%;
  text-align: center;
  p {
    font-family: $bevan;
    font-size: 60%;
    color: $college-station-tan;
    font-weight: lighter;
    margin-top: 0.46%;
    letter-spacing: 4vw;
  }
}

.original-div {
  margin-top: 54.75%;
  position: absolute;
  margin-left: $left-margin;
  p {
    font-family: $bevan;
    font-size: 115%;
    color: $aggie-tan;
    margin-bottom: 0.8vh;
    text-shadow: 1px 1px $shadow;
  }
}

.established-div {
  text-align: center;
  margin-left: 5%;
  margin-right: 5%;
  width: 90%;
  bottom: -17.5vh;
  position: absolute;
  clear: both;
  height: 45vh;
  // perspective: 1px;
  // overflow-x: hidden;
  // overflow-y: auto;
  h1 {
    font-family: $oswald;
    font-weight: bolder;
    font-size: 1250%;
    color: $established-brown;
    bottom: 0;
    position: absolute;
    letter-spacing: 1.85vw;
    text-shadow: 1px 1px;
    margin-left: -0.60vw;
    transition: 2s;
    // transform: translateZ(-1px) scale(2);
  }
}

.forty-years-div {
  position: absolute;
  bottom: 0.75vh;
  right: 5.5%;
  z-index: 1;
  display: flex;
  .h-top-arrow {
    width: 3vw;
    height: 12vw;
    transform: rotate(-90deg);
    margin-right: 7vw;
  }
  p {
    font-family: $bevan;
    font-size: 120%;
    color: $see-what-tan;
    margin-top: 10vh;
    span {
      color: $forty-years-white;
      border-bottom: 5px solid;
      border-color: $forty-years-maroon;
    }
  }
}

.bottom-bar-div {
  height: 4vh;
  width: 100%;
  background-color: $established-brown;
  bottom: 0;
  position: absolute;
}

.m-carousel-main {
  overflow: hidden;
}

.specials-store-section {
  height: 189vh;
  width: 100%;
  background: $main-white;
  position: relative;
}

.specials-div {
  height: 75%;
  width: 50%;
}

.specials {
  position: absolute;
  margin-left: 5%;
  margin-top: 10%;
  h1 {
    color: $established-brown;
    font-family: $oswald;
    font-size: 6vw;
    font-weight: bolder;
  }
  p {
    color: $forty-years-maroon;
    font-family: $bevan;
    font-size: 1.2vw;
  }
}

.three-days-div {
  height: 21%;
  width: 50%;
  top: 0;
  margin-top: 17%;
  position: absolute;
  padding-right: 7%;
}

.day-div {
  h1 {
    color: $dominoes-white;
    font-family: $oswald;
    font-size: 6vw;
    font-weight: bolder;
    transform: rotate(-90deg);
  }
}

.mon {
  position: absolute;
  top: 0;
  margin-left: 1%;
  margin-top: 7%;
}

.tue {
  position: absolute;
  top: 0;
  margin-left: 30%;
  margin-top: 9.25%;
}

.wed {
  position: absolute;
  top: 0;
  right: 0;
  margin-right: 24%;
  margin-top: 7.75%;
}

.thu {
  position: absolute;
  top: 0;
  margin-left: 1%;
  margin-top: 36.25%;
}

.sun {
  position: absolute;
  top: 0;
  margin-left: 14.25%;
  margin-top: 36.25%;
}

.day-div {
  h3 {
    color: $the-town-text-brown;
    font-family: $open-sans;
    font-weight: bold;
    font-size: 1.6vw;
  }
}

.day-div {
  p {
    color: $the-town-text-brown;
    font-family: $open-sans;
    font-size: 1.2vw;
  }
}

.d {
  height: 16vh;
  width: 12vw;
  position: absolute;
}

.day-1 {
  top: 0;
  margin-left: 10%;
  margin-top: 16%;
}

.day-2 {
  top: 0;
  margin-left: 37%;
  margin-top: 16%;
}

.day-3 {
  top: 0;
  right: 0;
  margin-top: 16%;
  margin-right: 12%;
}

.day-4 {
  top: 0;
  margin-top: 40%;
  margin-left: 5%;
}

.day-5 {
  top: 0;
  margin-top: 40%;
  margin-left: 18.5%;
}

.day-desc {
  margin-top: 5%;
}

.dominoes-div {
  color: $dominoes-white;
  font-family: $oswald;
  font-size: 4vw;
  position: absolute;
  font-weight: bolder;
  margin-left: 5%;
  margin-top: 52%;
}

.shop {
  height: 66.5vh;
  width: 50%;
  background: $shop-white;
  position: absolute;
  right: 0;
  top: 0;
  h1 {
    color: $established-brown;
    font-family: $oswald;
    font-size: 6vw;
    font-weight: bolder;
    padding-top: 30%;
    padding-left: 10%;
  }
  @keyframes bounce{
    0%, 20%, 50%, 80%, 100% {
      transform: translateY(0);
    }
    40% {
      transform: translateY(-30px);
    }
    60% {
      transform: translateY(-15px);
    }
  }
  .h-shop-arrow {
    width: 3vw;
    height: 12vw;
    z-index: 3;
    position: absolute;
    top: 9.75vh;
    right: 27vw;
  }
  .h-shop-arrow:hover {
    animation: bounce 2s;
    animation-iteration-count: 2;
  }
  .h-store-div {
    height: 9vh;
    width: 26vh;
    background: $visit-store-white;
    text-align: center;
    position: absolute;
    top: 0;
    right: 0;
    margin-right: 33%;
    margin-top: 33%;
    p {
      font-family: $bevan;
      color: $forty-years-maroon;
      margin-top: 14%;
    }
  }
  .h-store-div:hover {
    background: $store-hover;
    transition: 1.5s;
  }
}

.shirt-div {
  height: auto;
  width: 35vw;
  z-index: 1;
  top: 0;
  right: 0;
  position: absolute;
  margin-top: 23.85%;
  margin-right: 8%;
  img {
    height: 65vh;
    width: auto;
  }
}

.featured-div {
  height: 4vh;
  width: 12vw;
  z-index: 2;
  position: absolute;
  right: 0;
  margin-right: 35.5%;
  margin-top: 42%;
  transform: rotate(-90deg);
  display: flex;
  flex-direction: row;
  i {
    color: $star-grey;
    transform: rotate(-55deg);
    font-size: 125%;
  }
  h3 {
    font-family: $oswald;
    font-weight: bolder;
    color: $featured-grey;
    font-size: 2vw;
    margin-left: 0.5vh;
  }
}

.dons-main-div {
  .dons-album {
    img {
      position: absolute;
      bottom: 0;
      left: 0;
      height: 50vh;
      width: auto;
      border: $established-brown 1vw solid;
      margin-left: 5%;
      margin-bottom: 10%;
      z-index: 2;
    }
  }

  .march-div {
    background: $dons-box-white;
    position: absolute;
    bottom: 0;
    height: 48vh;
    width: 100%;
    margin-bottom: 10%;
    .march-text-div {
      margin-left: 53%;
      margin-top: 8.5%;
      .march {
        font-family: $bevan;
        color: $forty-years-maroon;
        font-size: 1.2vw;
      }
      h3 {
        font-family: $oswald;
        font-size: 2.5vw;
        color: $featured-grey;
        font-weight: bold;
        margin-bottom: 1vw;
      }
      .dons-text {
        font-family: $open-sans;
        font-size: 1.2vw;
        color: $willie-nelson-grey;
        line-height: 1.5vw;
      }
      h4 {
        font-family: $bevan;
        font-size: 1.2vw;
        color: $forty-years-maroon;
        border-bottom: $forty-years-maroon .35vh solid;
        width: 8.07vw;
        margin-left: 57.45%;
        margin-top: 5.5%;
        padding-bottom: .2vh;
      }
    }
  }

  .chicken-blog-div {
    background: $chicken-blog-grey;
    height: 9vh;
    width: 15vw;
    position: absolute;
    z-index: 1;
    bottom: 53vh;
    right: -1vh;
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;
    transform: rotate(-90deg);
    p {
      color: $chicken-blog-text;
      font-size: 1vw;
      font-family: $bevan;
      margin-right: 1vw;
      margin-left: 1vw;
    }
    i {
      color: $star-grey;
      font-size: 125%;
      transform: rotate(-55deg);
    }
  }
  .chicken-blog-div:hover {
    background: rgb(214, 210, 201);
    transition: 1.5s;
  }
  .h-chicken-arrow {
    width: 3vw;
    height: 12vw;
    z-index: 3;
    position: absolute;
    bottom: 33vh;
    right: 7vw;
  }

}

.our-friends-section {
  background: $main-white;
  height: 56vh;
  width: 100%;
  display: flex;
  flex-direction: row;
  position: relative;
}

.friends {
  height: 54vh;
  width: 100%;
  background-size: cover;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  position: relative;
  .overlay {
    position: absolute;
    background: #000;
    height: 100%;
    width: 100%;
    opacity: 0;
  }
  .overlay:hover {
    opacity: 0.5;
    transition: 1s;
  }
}

.dry-bean-div {
  background: url('./images/home/Dry Bean image.jpg') no-repeat;
  background-position: 40% 50%;
}

.chicken-oil-div {
  background: url('./images/home/chicken oil co image.jpg') no-repeat;
  background-position: 65% 50%;
}

.logo-bottom {
  // margin-bottom: 14vh;
  // margin-top: 16vh;
  z-index: 20;
  position: absolute;
}

.visit-website {
  margin-right: 18%;
  p {
    font-family: $oswald;
    font-size: 1.5vw;
    color: $main-white;
    border-bottom: $forty-years-maroon .25vw solid;
    font-weight: bold;
    width: 8.75vw;
    padding-bottom: 0.5%;
    z-index: 20;
    position: absolute;
    bottom: 10%;
  }
}

.chicken-oil-visit {
  left: 42%;
}

.visit-div {
  background: $our-friends-grey;
  font-family: $oswald;
  color: $featured-grey;
  font-size: 2vw;
  font-weight: bold;
  padding: 1vw 2vw;
  z-index: 20;
  position: absolute;
  transform: translateX(-50%);
  top: 2vw;
  left: 50%;
}

// MEDIA QUERY 1280  //////////////////////////////////////////////////////////////////////
@media (max-width: 1280px) {
  .h-top {
    height: 119vh;
    background-size: contain;
  }

  .explore {
    margin-right: 88px;
  }

  .aggie-tradition {
    .aggie {
      width: 29vw;
      .small-box {
        height: 0.6vh;
        width: 6.25vw;
      }
      h4 {
        font-size: 101%;
      }
    }
    .tradition {
      h1 {
        font-size: 638%;
        color: $tradition-white;
      }
    }
  }

  .college-station-div {
    height: 2.25vh;
    p {
      font-size: 30%;
    }
  }

  .original-div {
    margin-top: 54.25%;
    p {
    font-size: 95%;
    }
  }

  .established-div {
    bottom: -18.5vh;
    h1 {
      font-size: 1011%;
      margin-left: 0.7vw;
    }
  }

  .forty-years-div {
    bottom: -1.25vh;
    .h-top-arrow {
      height: 13.5vw;
    }
    p {
      font-size: 97%;
      margin-top: 10vh;
    }
  }

  .specials-store-section {
    height: 170vh;
  }

  .dominoes-div {
    margin-top: 54%;
  }

  .shop {
    height: 60.5vh;
    .h-shop-arrow {
      height: 11.25vw;
    }
    .h-store-div {
      height: 8vh;
      width: 23vh;
      p {
        font-size: 86%;
      }
    }
  }

  .shirt-div {
    img {
      height: 58vh;
    }
  }

  .featured-div {
    margin-right: 35%;
  }

  .dons-main-div {
    .dons-album {
      img {
        height: 45vh;
      }
    }
    .march-div {
      height: 43vh;
      .march-text-div {
        margin-left: 52.5%;
      }
    }

    .chicken-blog-div {
      bottom: 47vh;
      height: 8vh;
      i {
        font-size: 108%;
      }
    }
    .chicken-blog-div:hover {
      background: rgb(214, 210, 201);
      transition: 1.5s;
    }
    .h-chicken-arrow {
      bottom: 29vh;
    }

  }

  .our-friends-section {
    height: 52vh;
    .friends {
      height: 50vh;
    }
  }

}
